	module.exports = {"data":{"markdownRemark":{"html":"<h2>Visual Hierarchy</h2>\n<p>Visual hierarchy is the organization and presentation of content. You can use visual hierarchy to focus users’ attention and show where the most important information is.</p>\n<p>You can achieve visual hierarchy in many ways, including (but not limited to):</p>\n<ul>\n<li>Size</li>\n<li>Color and contrast</li>\n<li>Headings and subheadings</li>\n<li>Fonts</li>\n<li>White or blank space</li>\n<li>Texture</li>\n<li>Position</li>\n<li>Orientation</li>\n<li>Overall layout</li>\n</ul>\n<h4>More Information:</h4>\n<p>Tools to Create a Visual Hierarchy: <a href='https://designschool.canva.com/blog/visual-hierarchy/' target='_blank' rel='nofollow'>Design School blog</a></p>\n<p>6 Principles of Visual Hierarchy: <a href='https://99designs.com/blog/tips/6-principles-of-visual-hierarchy/' target='_blank' rel='nofollow'>99 designs blog</a></p>\n<p>Case Study - Improving a Page with Visual Hierarchy: <a href='http://blog.formedfunction.com/post/3029763425/on-visual-hierarchy' target='_blank' rel='nofollow'>blog</a></p>","fields":{"slug":"/visual-design/visual-hierarchy/"},"frontmatter":{"title":"Visual Hierarchy"}}},"pathContext":{"slug":"/visual-design/visual-hierarchy/"}}

